@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{min-height:100%;min-width:100%;background-image:linear-gradient(-225deg, #69eacb 0%, #eaccf8 48%, #6654f1 100%)}.App .search-form{display:flex;justify-content:center;align-items:center;min-height:20vh}.App .search-form .search-bar{width:40vw;padding:1rem;border:none;font-size:1.5rem}.App .search-form .search-bar:focus{outline:none}.App .search-form .search-button{font-size:1.5rem;padding:1rem;border:none;background-color:rgba(155,5,255,0.877);color:white}.App .search-form .search-button:focus{outline:none}.App .search-form .search-button:hover{cursor:pointer;opacity:0.8}.App .recipes{display:flex;flex-wrap:wrap;justify-content:space-around;padding:3rem}@media screen and (max-width: 768px){.App .search-form .search-bar{width:50%;font-size:1rem}.App .search-form .search-button{font-size:1rem}.App .recipes{padding:0}}

.recipe{display:flex;flex-direction:column;align-items:center;justify-content:space-around;width:35vw;height:auto;background-color:white;margin:1rem;border:none;border-radius:10px}.recipe:hover{box-shadow:0px 5px 20px #474747;-webkit-transition:all linear 0.3s;transition:all linear 0.3s}.recipe h1{text-transform:uppercase;font-family:Georgia, 'Times New Roman', Times, serif;text-align:center}.recipe img{border-radius:50%;margin:2rem;border:2px black solid;-webkit-transform:scale(0.8);transform:scale(0.8)}.recipe ol{font-family:'Raleway', sans-serif;font-size:1.2rem;margin:0.5rem;width:70%;line-height:1.3}.recipe p{font-family:'Raleway', sans-serif;font-size:1.5rem;text-align:center}.recipe p span{font-weight:bold}@media screen and (max-width: 768px){.recipe{width:90%}.recipe h1{font-size:1.3rem;margin:1rem}.recipe ol{font-size:1rem}.recipe p{font-size:1.3rem}.recipe img{-webkit-transform:scale(0.8);transform:scale(0.8);margin:0}}

